.App {
  font-family: sans-serif;
  text-align: center;
}
@keyframes plug {
  0% {
    transform: scale(0.5);
  }
  25% {
    transform: scale(0.75);
  }
  50% {
    transform: scale(1);
  }
  75% {
    transform: scale(1.25);
  }
  100% {
    transform: scale(1.5);
  }
}
@keyframes loader {
  0% {
    border-top: 1px dotted black;
  }
  25% {
    border-right: 1px dotted rgb(131, 216, 89);
  }
  50% {
    border-bottom: 1px dotted rgb(64, 127, 209);
  }
  75% {
    border-left: 1px dotted rgb(180, 37, 113);
  }
  100% {
    border-top: 1px dotted rgb(122, 7, 7);
  }
}
